import { useState } from 'react';
import useIsDesktop from '../../custom hooks/useIsDesktop';
import { AiOutlineMenu } from 'react-icons/ai';
import "./Header.css";

export default function Header () {
    
    //get wether the screen is desktop size or not
    const desktopView = useIsDesktop();

    return screenSize(desktopView);
}

function screenSize(desktop) {
    switch(desktop) {
        
        case true:
            return <DesktopHeader />

        case false:
            return <MobileHeader />
    }
}

function DesktopHeader() {
    return (
        <div className="header-box">
            <div className="logo"><a href="#">Tal Diamant</a></div>
            <div className="navigation-menu">
                <ul>
                    <li><a href="#">Home</a></li>
                    <li><a href="#projects">Projects</a></li>
                    {/* <li><a href="#projects">Games Projects</a></li> */}
                    <li><a href="#skills">Skills</a></li>
                    <li><a href="#resume">Resume</a></li>
                    <li><a href="#contact">Contact</a></li>
                </ul>
            </div>
        </div>
    )
}

function MobileHeader() {
    const [menuOpen, setMenuOpen] = useState(false);

    function handleClick() {
        setMenuOpen(prevState => !prevState);
    }

    return (
        <div className="header-box">
            <div className="logo">
                <a href="#">
                    <img src="/favicon.png" alt="logo" className="mobile-header-logo"/>
                </a>
            </div>
            <div className="navigation-menu">
                <div>
                    <button onClick={handleClick} className="mobile-menu-btn">
                        <AiOutlineMenu className="mobile-header-icon"/>
                    </button>
                </div>

                {menuOpen &&
                <div className="mobile-dropdown-menu" onClick={handleClick}>
                    <ul>
                        <li><a href="#">Home</a></li>
                        <li><a href="#projects">Projects</a></li>
                        <li><a href="#skills">Skills</a></li>
                        <li><a href="#resume">Resume</a></li>
                        <li><a href="#contact">Contact</a></li>
                    </ul>
                </div>}
            </div>
        </div>
    )
}