import { IconContext } from "react-icons";
import { FcGoogle } from "react-icons/fc";
import { AiFillLinkedin } from "react-icons/ai";
import useIsDesktop from '../../custom hooks/useIsDesktop';
import "./Contact.css";

export default function Contact() {
    const isDesktop = useIsDesktop();

    return screenSize(isDesktop);
}

function screenSize(desktop) {
    switch(desktop) {
        
        case true:
            return <DesktopContact />

        case false:
            return <MobileContact />
    }
}

function DesktopContact() {
    return (
        <>
        <a class="anchor" id="contact"></a>
        <section className="contact-section">
            <div className="section-title">
                <h2>Contact</h2>
            </div>
            <IconContext.Provider value={{ size: "2rem", color: "#55f", className: "global-class-name" }}>
            <div className="contact-blocks">
                <div className="contact-block">
                    <div class="contact-grid-item"><FcGoogle /></div> <div class="contact-grid-item">erkeliwood@gmail.com</div>
                    <div class="contact-grid-item"><AiFillLinkedin /></div> <div class="contact-grid-item"><a href="https://il.linkedin.com/in/tal-diamant">Linkedin</a></div>                    
                </div>
                <div className="contact-block"></div>
            </div>
            </IconContext.Provider>
        </section>
        </>
    );
}

function MobileContact() {
    return (
        <>
        <a class="anchor" id="contact"></a>
        <section className="mobile-contact-section">
            <div className="mobile-section-title">
                <h2>Contact</h2>
            </div>
            <IconContext.Provider value={{ size: "2rem", color: "#55f", className: "global-class-name" }}>
            <div className="mobile-contact-blocks">
                <div className="mobile-contact-block">
                    <div className="mobile-contact-grid-item"><FcGoogle /></div> <div className="mobile-contact-grid-item">erkeliwood@gmail.com</div>
                    <div className="mobile-contact-grid-item"><AiFillLinkedin /></div> <div className="mobile-contact-grid-item"><a href="https://il.linkedin.com/in/tal-diamant">Linkedin</a></div>                    
                </div>
                <div className="mobile-contact-block"></div>
            </div>
            </IconContext.Provider>
        </section>
        </>
    );
}