import useIsDesktop from '../../custom hooks/useIsDesktop';
import "./Footer.css";

export default function Footer() {
    const isDesktop = useIsDesktop();

    return screenSize(isDesktop);
}

function screenSize(desktop) {
    switch(desktop) {
        
        case true:
            return <DesktopFooter />

        case false:
            return <MobileFooter />
    }
}

function DesktopFooter() {
    return (
        <>
        <a class="anchor" id="footer"></a>
        <div className="my-footer">
        © TAL DIAMANT {new Date().getFullYear()}. ALL RIGHTS RESERVED.
        </div>
        </>
    );
}

function MobileFooter() {
    return (
        <>
        <a class="anchor" id="footer"></a>
        <div className="mobile-my-footer">
        © TAL DIAMANT {new Date().getFullYear()}. ALL RIGHTS RESERVED.
        </div>
        </>
    );
}