import useIsDesktop from '../../custom hooks/useIsDesktop';
import "./Skills.css";

export default function Skills() {
    const isDesktop = useIsDesktop();

    return screenSize(isDesktop);
}

function screenSize(desktop) {
    switch(desktop) {
        
        case true:
            return <DesktopSkills />

        case false:
            return <MobileSkills />
    }
}

function DesktopSkills() {
    return (
        <>
        <a className="anchor" id="skills"></a>
        <section className="skills-section">
            <div className="section-title">
                <h2>Skills</h2>
                <div className="skill-blocks">
                    <div className="blocks">
                        <ul>
                            <li>HTML</li>
                            <li>CSS</li>
                            <li>JavaScript</li>
                            <li>React</li>
                            <li>Redux</li>
                        </ul>
                    </div>
                    <div className="blocks">
                        <ul>
                            <li>Node.js</li>
                            <li>Express</li>
                            <li>MongoDB</li>
                            <li>React Native</li>
                            <li>Typescript</li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
        </>
        );
}

function MobileSkills() {
    return (
        <>
        <a className="anchor" id="skills"></a>
        <section className="mobile-skills-section">
            <div className="mobile-section-title">
                <h2>Skills</h2>
                <div className="mobile-skill-blocks">
                    <div className="mobile-blocks">
                        <ul>
                            <li>HTML</li>
                            <li>CSS</li>
                            <li>JavaScript</li>
                            <li>React</li>
                            <li>Redux</li>
                        </ul>
                    </div>
                    <div className="mobile-blocks">
                        <ul>
                            <li>Node.js</li>
                            <li>Express</li>
                            <li>MongoDB</li>
                            <li>React Native</li>
                            <li>Typescript</li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
        </>
    );
}