import useIsDesktop from '../../custom hooks/useIsDesktop';
import "./Resume.css";

export default function Resume() {
    const isDesktop = useIsDesktop();

    return screenSize(isDesktop);
}

function screenSize(desktop) {
    switch(desktop) {
        
        case true:
            return <DesktopResume />

        case false:
            return <MobileResume />
    }
}

function DesktopResume() {
    return (
        <>
        <a class="anchor" id="resume"></a>
        <section className="resume-section">
            <div className="section-title">
                <h2>Resume</h2>
            </div>
            <div className="resume-blocks">
                <div className="resume-block">
                    <img className="resume-img" src="/resume transparent.png" alt="resume icon image" />
                </div>
                <div className="resume-block">
                    <div>
                        <a href="/Resume.pdf" download><button className="resume-btn">Download</button></a>
                    </div>
                </div>
            </div>
        </section>
        </>
    );
}

function MobileResume() {
    return (
        <>
        <a class="anchor" id="resume"></a>
        <section className="mobile-resume-section">
            <div className="mobile-section-title">
                <h2>Resume</h2>
            </div>
            <div className="mobile-resume-blocks">
                <div className="mobile-resume-block">
                    <img className="mobile-resume-img" src="/resume transparent.png" alt="resume icon image" />
                </div>
                <div className="mobile-resume-block">
                    <div>
                        <a href="/Resume.pdf" download><button className="mobile-resume-btn">Download</button></a>
                    </div>
                </div>
            </div>
        </section>
        </>
    );
}


